import React from "react";
import SignForm from "./SignForm";
import Loader from "./Loader.js";

function Login({ onLogin, isLoading }) {
  return (
    <>
      <Loader isLoading={isLoading} />
      {!isLoading && (
        <SignForm
          title=""
          name="login"
          buttonSubmitText=""
          onSubmit={onLogin}
        />
      )}
    </>
  );
}

export default Login;
